import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AuthGuard } from '@core/guards/auth.guard';
import { NoAuthGuard } from '@core/guards/no-auth.guard';
import { CustomLayoutComponent } from '@layout/custom-layout.component';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('@home/home.module').then(m => m.HomeModule),
    pathMatch: 'full',
    canActivate: [NoAuthGuard],
  },
  {
    path: 'subscriptions',
    component: CustomLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('@subscriptions/subscriptions.module').then(m => m.SubscriptionsModule),
      },
    ],
  },
  {
    path: 'login',
    loadChildren: () => import('@auth/pages/login/login.module').then(m => m.LoginModule),
    canActivate: [NoAuthGuard],
  },
  {
    path: 'register',
    loadChildren: () => import('@auth/pages/register/register.module').then(m => m.RegisterModule),
    canActivate: [NoAuthGuard],
  },
  {
    path: 'forgot-password',
    loadChildren: () => import('@auth/pages/forgot-password/forgot-password.module').then(m => m.ForgotPasswordModule),
  },
  {
    path: 'projects',
    component: CustomLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('@projects/projects.module').then(m => m.ProjectsModule),
        canActivate: [AuthGuard],
      },
    ],
  },
  {
    path: 'scenarios',
    component: CustomLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('@scenarios/scenarios.module').then(m => m.ScenariosModule),
        canActivate: [AuthGuard],
      },
    ],
  },
  {
    path: 'requests',
    component: CustomLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('@requests/requests.module').then(m => m.RequestsModule),
        canActivate: [AuthGuard],
      },
    ],
  },
  {
    path: 'endpoints',
    component: CustomLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('@endpoints/endpoints.module').then(m => m.EndpointsModule),
        canActivate: [AuthGuard],
      },
    ],
  },
  {
    path: 'aliases',
    component: CustomLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('@aliases/aliases.module').then(m => m.AliasesModule),
        canActivate: [AuthGuard],
      },
    ],
  },
  {
    path: 'users',
    component: CustomLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('@users/users.module').then(m => m.UsersModule),
        canActivate: [AuthGuard],
      },
    ],
  },
  {
    path: 'organizations',
    component: CustomLayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () => import('@organizations/organizations.module').then(m => m.OrganizationsModule),
        canActivate: [AuthGuard],
      },
    ],
  },
  {
    path: '**',
    loadChildren: () => import('@error/pages/404/error-404.module').then(m => m.Error404Module),
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      // preloadingStrategy: PreloadAllModules,
      scrollPositionRestoration: 'enabled',
      relativeLinkResolution: 'corrected',
      anchorScrolling: 'enabled',
    }),
  ],
  exports: [RouterModule],
})

export class AppRoutingModule {
}
