import { Injectable } from '@angular/core';

import { RestApiService } from './rest-api.service';

@Injectable({
  providedIn: 'root',
})
export class HeaderNameResource {
  ENDPOINT_ENDPOINT = 'endpoints';
  ENDPOINT = 'header_names';

  constructor(private restApi: RestApiService) { }

  index(endpointId, queryParams?: object) {
    if (typeof endpointId === 'object') {
      queryParams = endpointId;
      return this.restApi.index(
        [this.ENDPOINT], queryParams
      );
    } else {
      return this.restApi.index(
        [this.ENDPOINT_ENDPOINT, endpointId, this.ENDPOINT], queryParams
      );
    }
  }

  show(endpointId, id: number, queryParams?: object) {
    return this.restApi.show(
      [this.ENDPOINT_ENDPOINT, endpointId, this.ENDPOINT, id], queryParams
    );
  }

  create(endpointId, body: object) {
    return this.restApi.create(
      [this.ENDPOINT_ENDPOINT, endpointId, this.ENDPOINT], body
    );
  }

  update(endpointId, id, body: object) {
    return this.restApi.update(
      [this.ENDPOINT_ENDPOINT, endpointId, this.ENDPOINT, id], body
    );
  }

  destroy(endpointId, id) {
    return this.restApi.destroy(
      [this.ENDPOINT_ENDPOINT, endpointId, this.ENDPOINT, id]
    );
  }
}
