import { Alias } from './alias';

export class RequestComponent {
  type: number;
  id: number;
  alias: Alias;
  aliasName: string;

  constructor(type: number, id: number, alias?: Alias) {
    this.type = type;
    this.id = id;
    this.alias = alias;
    if (alias) {
      this.aliasName = alias.name;
    }
  }
}

export enum RequestComponentType {
  Header = 1,
  PathSegment,
  QueryParam,
  BodyParam,
  Response,
  ResponseHeader,
}
