export class Plan {
  id: number;
  name: string;
  description: string;
  chargeAmount: number;
  chargeInDays: number;
  createdAt: Date;
  updatedAt: Date;

  constructor(plan) {
    this.id = plan.id;
    this.name = plan.name;
    this.description = plan.description;
    this.createdAt = new Date(plan.created_at);
    this.updatedAt = new Date(plan.updated_at);
    this.chargeAmount = plan.charge_amount;
    this.chargeInDays = plan.chage_in_days;
  }
}
