import { RequestComponent } from './request-component';

export class Response extends RequestComponent {
  text: string;
  isJson: boolean;

  constructor(response) {
    super(5, response.id);

    this.text = response.text;
    if (response.is_json !== undefined) {
      this.isJson = response.is_json;
    } else {
      this.isJson = true;
    }
  }
}
