import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LayoutService {

  private _quickpanelOpenSubject = new BehaviorSubject<boolean>(false);
  quickpanelOpen$ = this._quickpanelOpenSubject.asObservable();

  private _sidenavOpenSubject = new BehaviorSubject<boolean>(false);
  sidenavOpen$ = this._sidenavOpenSubject.asObservable();

  // Controls whether sidenav is expanded false = expanded
  private _sidenavCollapsedSubject = new BehaviorSubject<boolean>(true);
  sidenavCollapsed$ = this._sidenavCollapsedSubject.asObservable();

  // Controls whether sidenav is overlayed true = expanded
  private _sidenavCollapsedOpenSubject = new BehaviorSubject<boolean>(false);
  sidenavCollapsedOpen$ = this._sidenavCollapsedOpenSubject.asObservable();

  private _configpanelOpenSubject = new BehaviorSubject<boolean>(false);
  configpanelOpen$ = this._configpanelOpenSubject.asObservable();

  private _searchOpen = new BehaviorSubject<boolean>(false);
  searchOpen$ = this._searchOpen.asObservable();

  constructor(private router: Router) { 
    this.sidenavOpen$.subscribe(e => {
      console.log('sidenavOpen');
      console.log(e);
    })

    this.sidenavCollapsed$.subscribe(e => {
      console.log('sidenavCollapsed');
      console.log(e);
    })

    this.sidenavCollapsedOpen$.subscribe(e => {
      console.log('sidenavCollapsedOpen')
      console.log(e)
    })
  }

  openQuickpanel() {
    this._quickpanelOpenSubject.next(true);
  }

  closeQuickpanel() {
    this._quickpanelOpenSubject.next(false);
  }

  openSidenav() {
    console.log('openSidenav')
    this._sidenavOpenSubject.next(true);
  }

  closeSidenav() {
    console.log('closeSidenav');
    this._sidenavOpenSubject.next(false);
  }

  collapseSidenav() {
    console.log('collapseSidenav')
    this._sidenavCollapsedSubject.next(true);
  }

  expandSidenav() {
    console.log('expandSidenav');
    this._sidenavCollapsedSubject.next(false);
  }

  // Shows sidenav as overlay
  collapseOpenSidenav() {
    console.log('collapseOpenSidenav');
    this._sidenavCollapsedOpenSubject.next(true);
  }

  // Hides sidenav as overlay
  collapseCloseSidenav() {
    console.log('collapseCloseSidenav');
    this._sidenavCollapsedOpenSubject.next(false);
  }

  openConfigpanel() {
    this._configpanelOpenSubject.next(true);
  }

  closeConfigpanel() {
    this._configpanelOpenSubject.next(false);
  }

  openSearch() {
    this._searchOpen.next(true);
  }

  closeSearch() {
    this._searchOpen.next(false);
  }

  enableRTL() {
    this.router.navigate([], {
      queryParams: {
        rtl: 'true'
      }
    }).then(() => {
      if (window) {
        window.location.reload();
      }
    });
  }

  disableRTL() {
    this.router.navigate([], {
      queryParams: {
        rtl: 'false'
      }
    }).then(() => {
      if (window) {
        window.location.reload();
      }
    });
  }
}
