import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { ProjectResource } from '@core/http/project-resource.service';
import { Organization, Project } from '@data/schema';

@Injectable({
  providedIn: 'root',
})
export class ProjectsDataService {
  private subject = new BehaviorSubject([]);
  projects$ = this.subject.asObservable();
  projects: Project[];

  constructor(
    private projectResource: ProjectResource
  ) { }

  fetch(organizationId: Organization['id']) {
    const queryParams = {
      organization_id: organizationId,
    };

    this.projectResource.index(queryParams).subscribe((projects: Project[]) => {
      this.set(projects.map(p => {
        return new Project(p);
      }));
    });
  }

  get() {
    if (this.projects) { return this.projects$; }

    return this.projects$;
  }

  set(projects: Project[]) {
    this.projects = projects;
    this.subject.next(projects);
  }
}
