export class ProjectsUser {
  id: number;
  name: string;
  email: string;
  role: string;

  constructor(projectsUser) {
    this.id = projectsUser.id;
    this.name = projectsUser.name;
    this.email = projectsUser.email;
    this.role = projectsUser.role;
  }
}

export enum ProjectsUserRoles {
  project_owner = 1,
  project_admin,
  project_editor,
  project_viewer,
}

export enum ProjectsUserRoleTitles {
  Owner = 1,
  Admin,
  Editor,
  Viewer,
}
