import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { environment } from '@environments/environment';
import { UrlBuilder } from './url-builder';

@Injectable({
  providedIn: 'root',
})
export class HttpService {
  constructor(
    private http: HttpClient
  ) { }

  mergeQueryParams(url, params) {
    let queryString = '';

    for (const key in params) {
      if (!queryString) {
        queryString = `?${encodeURIComponent(key)}=${encodeURIComponent(params[key])}`;
      } else {
        queryString += `&${encodeURIComponent(key)}=${encodeURIComponent(params[key])}`;
      }
    }

    return url + queryString;
  }

  get(url: string, params?: object, options?: object) {
    return this.http.get(this.mergeQueryParams(url, params), options);
  }

  post(url: string, postBody: object, options?: object) {
    return this.http.post(url, postBody, options);
  }

  delete(url: string, params?: object, options?: object) {
    return this.http.delete(this.mergeQueryParams(url, params));
  }

  put(url: string, putData: object, options?: object) {
    return this.http.put(url, putData, options);
  }

  upload(url: string, file: File, options?: object) {
    const formData: FormData = new FormData();

    if (file) {
      formData.append('files', file, file.name);
    }

    return this.post(url, formData, options);
  }

  buildUrl(pathComponents: (string | number)[], queryParams?: object) {
    const urlBuilder = new UrlBuilder();
    urlBuilder.withPath(environment.apiUrl);

    for (const pathComponent of pathComponents) {
      urlBuilder.
         withPath(pathComponent.toString());
    }

    for (const queryParam in queryParams) {
      if (queryParams.hasOwnProperty(queryParam)) {
        urlBuilder.
           search(queryParam, queryParams[queryParam]);
      }
    }

    return urlBuilder.url;
  }
}
