export class Request {
  components: any;
  createdAt: Date;
  endpoint: string;
  endpointId: number;
  host: string;
  id: number;
  latency: number;
  method: string;
  port: string;
  position: number;
  priority: number;
  projectId: number;
  rollupId: number;
  starred: boolean;
  status: number;
  url: string;

  headers?: any;
  queryParams?: any;

  constructor(request) {
    this.components = request.components;
    this.createdAt = new Date(request.created_at);
    this.endpoint = request.endpoint;
    this.endpointId = request.endpoint_id;
    this.host = request.host;
    this.id = request.id;
    this.latency = request.latency;
    this.method = request.method;
    this.port = request.port;
    this.position = request.position;
    this.priority = 0;
    this.rollupId = request.rollup_id;
    this.projectId = request.project_id;
    this.starred = false;
    this.status = request.status;
    this.url = request.url;

    if (request.headers) {
      this.headers = request.headers;
    }

    if (request.queryParams) {
      this.queryParams = request.query_params;
    }
  }
}

export enum RequestPriorities {
  None = 0,
  Low,
  Medium,
  High,
}

