import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { ProjectResource } from '@core/http/project-resource.service';
import { ProjectResponse } from '@data/interfaces';
import { Project } from '@data/schema/project';

@Injectable()
export class ProjectDataService {
  private subject = new BehaviorSubject(null);
  project$ = this.subject.asObservable();
  project: Project;

  constructor(
    private projectResource: ProjectResource
  ) { }

  fetch(id: Project['id']) {
    this.projectResource.show(id).subscribe((res: ProjectResponse) => {
      this.set(new Project(res));
    });
  }

  get() {
    if (this.project) { return this.project$; }

    return this.project$;
  }

  set(project: Project) {
    this.project = project;
    this.subject.next(project);
  }
}
