export class Endpoint {
  id: number;
  method: string;
  path: string;
  components: any;
  category: number;

  constructor(endpoint) {
    this.id = endpoint.id;
    this.method = endpoint.method;
    this.path = endpoint.path;
    this.components = endpoint.components;
    this.category = endpoint.category;
  }
}

export enum EndpointCategories {
  Fixed = 1,
  Alias,
  Unknown,
}
