import { Injectable } from '@angular/core';

import { HttpService } from './http.service';

@Injectable({
  providedIn: 'root',
})
export class RestApiService {

  constructor(private httpService: HttpService) { }

  index(pathComponents: (string | number)[], queryParams?: object) {
    const url: string = this.httpService.buildUrl(pathComponents, queryParams);
    return this.httpService.get(url);
  }

  show(pathComponents: (string | number)[], queryParams?: object) {
    const url: string = this.httpService.buildUrl(pathComponents, queryParams);
    return this.httpService.get(url);
  }

  create(pathComponents: (string | number)[], body?: object) {
    const url: string = this.httpService.buildUrl(pathComponents);
    return this.httpService.post(url, body);
  }

  update(pathComponents: (string | number)[], body?: object) {
    const url: string = this.httpService.buildUrl(pathComponents);
    return this.httpService.put(url, body);
  }

  destroy(pathComponents: (string | number)[], queryParams?: object) {
    const url: string = this.httpService.buildUrl(pathComponents, queryParams);
    return this.httpService.delete(url);
  }

  path(pathComponents: (string | number)[], queryParams?: object) {
    return this.httpService.buildUrl(pathComponents, queryParams);
  }
}
