import { Clipboard } from '@angular/cdk/clipboard';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';

import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

import icClose from '@iconify/icons-ic/twotone-close';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icFileCopy from '@iconify/icons-ic/twotone-file-copy';

import { ScenarioResource } from '@core/http/scenario-resource.service';
import { ScenariosIndexResponse } from '@data/interfaces';
import { Project, Scenario } from '@data/schema';
import { ProjectDataService } from '@projects/services/project-data.service';
import { ProjectsDataService } from '@projects/services/projects-data.service';

@Component({
  selector: 'vex-toolbar-mock-url',
  templateUrl: './toolbar-mock-url.component.html',
  styleUrls: ['./toolbar-mock-url.component.scss'],
})
export class ToolbarMockUrlComponent implements OnInit {
  project: Project;
  scenarios: Scenario[] = [];

  projects$: Observable<Project[]>;

  icClose = icClose;
  icDelete = icDelete;
  icFileCopy = icFileCopy;

  form: FormGroup;

  constructor(
    private clipboard: Clipboard,
    private dialogRef: MatDialogRef<ToolbarMockUrlComponent>,
    private fb: FormBuilder,
    private projectDataService: ProjectDataService,
    private scenarioResource: ScenarioResource,
    private snackbar: MatSnackBar,
    private projectsDataService: ProjectsDataService
  ) { }

  ngOnInit() {
    // Initialize form
    this.form = this.fb.group({
      project: new FormControl(null, [Validators.required]),
      scenario: new FormControl(null),
      mockUrl: new FormControl('', [Validators.required]),
    });

    // This will only be set if the user is viewing a project
    this.project = this.projectDataService.project;
    this.handleProjectSelect(this.project);

    this.initializeProjects$();
    this.initializeScenarios();
  }

// API Access

  getScenarios(projectId?: Project['id']) {
    projectId = projectId || this.project.id;

    return this.scenarioResource.index(
      { project_id: projectId }
    ).pipe(
      tap((res: ScenariosIndexResponse) => {
        this.scenarios = res.list.map(s => {
          return new Scenario(s);
        });
      })
    );
  }

// View Access

  clearScenario() {
    this.form.patchValue({
      scenario: null,
    });

    this.handleProjectSelect(this.project);
  }

  copyUrl($event) {
    const url = this.form.value.mockUrl;
    this.clipboard.copy(url);

    this.snackbar.open('URL copied to clipboard!', 'close', {
      duration: 2000,
    });
  }

  selectProject($event) {
    const project: Project = $event.value;
    this.handleProjectSelect(project);
  }

  selectScenario($event) {
    const scenario: Scenario = $event.value;

    this.form.patchValue({
      mockUrl: scenario.mockUrl,
    });
  }

// Helpers

  handleProjectSelect(project) {
    if (!project) { return; }

    this.getScenarios(project.id).subscribe();

    this.form.patchValue({
      project,
      scenario: null,
      mockUrl: project.mockUrl,
    });

    this.project = project;
  }

  // Set projects$ observable
  initializeProjects$() {
    if (!this.projectsDataService.projects) {
      this.projectsDataService.fetch(this.project.organizationId);
    }

    this.projects$ = this.projectsDataService.projects$;
    this.projects$.subscribe((projects: Project[]) => {

    });
  }

  initializeScenarios() {
    if (this.project) {
      this.getScenarios(this.project.id).subscribe(res => {

      });
    }
  }

  /**
   *
   * Use to set default value for mat-select with reactive forms
   *
   * @param o1
   * @param o2
   * @returns [Boolean]
   *
   */
  compareProjects(o1: Project, o2: Project) {
    return (o1.name == o2.name);
  }
}
