export class User {
  id: number;
  email: string;
  name: string;
  lastSignInAt: Date;
  apiKey: string;
  createdAt: Date;

  constructor(user) {
    this.id = user.id;
    this.email = user.email;
    this.name = user.name;
    this.lastSignInAt = new Date(user.last_sign_in_at);
    this.createdAt = new Date(user.created_at);
    this.apiKey = user.api_key;
  }
}
