import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { UserResource } from '@core/http/user-resource.service';
import { User } from '@data/schema/user';

@Injectable({
  providedIn: 'root',
})
export class UserDataService {
  private subject = new BehaviorSubject(null);
  user$ = this.subject.asObservable();
  user: User;

  constructor(
    private userResource: UserResource
  ) { }

  get() {
    if (this.user) { return this.user$; }

    this.userResource.profile().subscribe(res => {
      this.set(new User(res));
    });

    return this.user$;
  }

  set(user: User | UserResponse) {
    if (user.hasOwnProperty('created_at')) {
      user = new User(user);
    } 

    this.user = user as User;
    this.subject.next(user as User);
  }
}
