import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';

import { AngularTokenService } from 'angular-token';
import { throwError } from 'rxjs';

import { LocalStorageConstants } from '@data/constants/local-storage.constants';
import { RouteConstants } from '@data/constants/route.constants';

@Injectable()
export class HttpErrorHandler {
  public onlineFlag = navigator.onLine;

  constructor(
    private router: Router,
    public snackbar: MatSnackBar,
    private tokenService: AngularTokenService
  ) {}

  public handleError(err: any) {
    let message = '';

    switch (err.status) {
      case 401:
        this.tokenService.signOut().subscribe(
          res => {
            this.router.navigate([RouteConstants.LOGIN_PAGE]);
          },
          res => {
            this.router.navigate([RouteConstants.LOGIN_PAGE]);
          }
        );
        break;
      case 404:
        message = 'The requested resource could not be found';
        this.snackbar.open(message, 'close');
        break;
      default:
        if (err.status >= 500) {
          message = 'An expected error occurred, please try again later';
        } else {
          message = err.error;
        }

        if (typeof message === 'string') {
          this.snackbar.open(message, 'close');
        }
     }

    return throwError(err);
   }
}
