import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

import icClose from '@iconify/icons-ic/twotone-close';
import icDelete from '@iconify/icons-ic/twotone-delete';

import { AgentService } from '@core/http/agent.service';
import { ScenarioResource } from '@core/http/scenario-resource.service';
import { ScenariosIndexResponse } from '@data/interfaces';
import { Project, Scenario } from '@data/schema';
import { ProjectDataService } from '@projects/services/project-data.service';
import { ProjectsDataService } from '@projects/services/projects-data.service';
import { UserDataService } from '@users/services/user-data.service';

import { AgentConfigResponse } from '../../interfaces/agent-config-response.interface';
import { AgentConfig } from '../../schema/agent-config';

@Component({
  selector: 'vex-toolbar-record-settings',
  templateUrl: './toolbar-record-settings.component.html',
  styleUrls: ['./toolbar-record-settings.component.scss'],
})
export class ToolbarRecordSettingsComponent implements OnInit {
  @Output() onProjectChange = new EventEmitter<Project>();
  @Output() onScenarioChange = new EventEmitter<Scenario>();

  config: AgentConfig;
  policies: Array<string>;
  project: Project;
  scenarios: Scenario[] = [];

  projects$: Observable<Project[]>;

  icClose = icClose;
  icDelete = icDelete;

  form: FormGroup;

  constructor(
    private dialogRef: MatDialogRef<ToolbarRecordSettingsComponent>,
    private fb: FormBuilder,
    private agentService: AgentService,
    private projectDataService: ProjectDataService,
    private scenarioResource: ScenarioResource,
    private userDataService: UserDataService,
    private projectsDataService: ProjectsDataService
  ) { }

  ngOnInit() {
    // Initialize form
    this.form = this.fb.group({
      excludePatterns: this.fb.array([]),
      includePatterns: this.fb.array([]),
      project: new FormControl(null, [Validators.required]),
      recordPolicy: new FormControl('all', [Validators.required]),
      scenario: new FormControl(null),
      serviceUrl: new FormControl(''),
    });

    this.initializePolicies();

    this.initializeConfig().subscribe((res: AgentConfigResponse) => {
      this.initializeProjects$(this.config);
      this.initializeProject(this.config);
      this.initializeScenarios(this.config);
    });
  }

// API Access

  getScenarios(projectId?: Project['id']) {
    projectId = projectId || this.project.id;

    return this.scenarioResource.index(
      { project_id: projectId }
    ).pipe(
      tap((res: ScenariosIndexResponse) => {
        this.scenarios = res.list;
      })
    );
  }

// View Access

  clearScenario() {
    this.form.patchValue({
      scenario: null,
    });
  }

  clearServiceUrl() {
    this.form.patchValue({
      serviceUrl: null,
    });
  }

  addIncludePattern(pattern?: string): void {
    const includePatterns: FormArray = this.form.get('includePatterns') as FormArray;

    includePatterns.push(
      this.fb.group({
        pattern: new FormControl(pattern || '', [Validators.required]),
      })
    );
  }

  removeIncludePattern(index: number) {
    const includePatterns: FormArray = this.form.get('includePatterns') as FormArray;

    includePatterns.removeAt(index);
  }

  addExcludePattern(pattern?: string): void {
    const excludePatterns: FormArray = this.form.get('excludePatterns') as FormArray;

    excludePatterns.push(
      this.fb.group({
        pattern: new FormControl(pattern || '', [Validators.required]),
      })
    );
  }

  removeExcludePattern(index: number) {
    const excludePatterns: FormArray = this.form.get('excludePatterns') as FormArray;

    excludePatterns.removeAt(index);
  }

  selectProject($event) {
    const project: Project = $event.value;

    this.getScenarios(project.id).subscribe();

    this.form.patchValue({
      scenario: null,
    });

    this.project = project;
  }

  update() {
    const formValue = this.form.value;

    const user = this.userDataService.user;
    const project = formValue.project;
    const config = {
      api_key: user.apiKey,
      mode: {
        record: {
          active: this.config.mode.record.active,
          exclude_patterns: formValue.excludePatterns.map(data => data.pattern),
          include_patterns: formValue.includePatterns.map(data => data.pattern),
          policy: formValue.recordPolicy,
          project_key: formValue.project.key,
          scenario_key: '',
          service_url: formValue.serviceUrl || '',
        },
      },
    };

    if (formValue.scenario) {
      const scenario: Scenario = formValue.scenario;
      config.mode.record.scenario_key = scenario.key;

      this.onScenarioChange.emit(scenario);
    }

    this.onProjectChange.emit(project);

    if (config) {
      this.agentService.updateConfig(config).subscribe(res => {
        this.dialogRef.close();
      });
    } else {
      this.agentService.createConfig(config).subscribe(res => {
        this.dialogRef.close();
      });
    }
  }

// Helpers

  initializePolicies() {
    this.agentService.showConfigsPolicies().subscribe(res => {
      this.policies = Object.values(res);
    });
  }

  initializeConfig(): Observable<AgentConfigResponse> {
    return this.agentService.showConfig().pipe(
      tap((res: AgentConfigResponse) => {
        const config: AgentConfigResponse = res;

        if (config) {
          this.config = new AgentConfig(config);

          this.form.patchValue({
            recordPolicy: this.config.mode.record.policy,
            serviceUrl: this.config.mode.record.serviceUrl,
          });

          this.config.mode.record.includePatterns.forEach(pattern => {
            this.addIncludePattern(pattern);
          });

          this.config.mode.record.excludePatterns.forEach(pattern => {
            this.addExcludePattern(pattern);
          });
        }
      })
    );
  }

  // Set projects$ observable
  initializeProjects$(config) {
    if (!this.projectsDataService.projects) {
      const organizationId = this.projectDataService.project.organizationId;
      this.projectsDataService.fetch(organizationId);
    }

    this.projects$ = this.projectsDataService.projects$;
  }

  initializeProject(config) {
    if (!config.mode.record.projectKey) {
      this.project = this.projectDataService.project;
      this.form.patchValue({
        project: this.project,
      });

      this.update();
    } else {
      this.projects$.subscribe((projects: Project[]) => {
        projects.some(project => {
          if (config.mode.record.projectKey === project.key) {
            this.project = project;

            this.form.patchValue({
              project,
            });

            return true;
          }
        });
      });
    }
  }

  initializeScenarios(config) {
    if (this.project) {
      this.getScenarios(this.project.id).subscribe(res => {
        this.scenarios.some(scenario => {
          if (config.mode.record.scenarioKey === scenario.key) {
            this.form.patchValue({
              scenario,
            });

            return true;
          }
        });
      });
    }
  }
}
