import { Injectable } from '@angular/core';

import { RestApiService } from './rest-api.service';

@Injectable({
  providedIn: 'root',
})
export class ResponseResource {
  REQUEST_ENDPOINT = 'requests';
  ENDPOINT = 'responses';

  constructor(private restApi: RestApiService) { }

  index(requestId, queryParams?: object) {
    return this.restApi.index(
      [this.REQUEST_ENDPOINT, requestId, this.ENDPOINT], queryParams
    );
  }

  show(requestId, id: number, queryParams?: object) {
    return this.restApi.show(
      [this.REQUEST_ENDPOINT, requestId, this.ENDPOINT, id], queryParams
    );
  }

  create(requestId, body: object) {
    return this.restApi.create(
      [this.REQUEST_ENDPOINT, requestId, this.ENDPOINT], body
    );
  }

  update(requestId, id, body: object) {
    return this.restApi.update(
      [this.REQUEST_ENDPOINT, requestId, this.ENDPOINT, id], body
    );
  }

  destroy(requestId, id) {
    return this.restApi.destroy(
      [this.REQUEST_ENDPOINT, requestId, this.ENDPOINT, id]
    );
  }
}
