import { Clipboard } from '@angular/cdk/clipboard';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';

import icArrowDropDown from '@iconify/icons-ic/twotone-arrow-drop-down';
import icFileCopy from '@iconify/icons-ic/twotone-file-copy';
import icPlayArrow from '@iconify/icons-ic/twotone-play-arrow';
import icSettings from '@iconify/icons-ic/twotone-settings';
import icStop from '@iconify/icons-ic/twotone-stop';

import { ProjectResource, ScenarioResource } from '@core/http';
import { AgentService } from '@core/http/agent.service';
import { Project, Scenario } from '@data/schema';
import { environment } from '@environments/environment';

import { ToolbarMockSettingsComponent } from './components/toolbar-mock-settings/toolbar-mock-settings.component';
import { ToolbarRecordSettingsComponent } from './components/toolbar-record-settings/toolbar-record-settings.component';
import { ModesResponse } from './interfaces/modes-response.interface';

@Component({
  selector: 'vex-toolbar-proxy-settings',
  templateUrl: './toolbar-proxy-settings.component.html',
  styleUrls: ['./toolbar-proxy-settings.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ToolbarProxySettingsComponent implements OnInit {
  @ViewChild('originRef', { static: true, read: ElementRef }) originRef: ElementRef;

  modes: ModesResponse;
  activeProject: Project;
  activeScenario: Scenario;

  dropdownOpen: boolean;
  icArrowDropDown = icArrowDropDown;
  icSettings = icSettings;
  icFileCopy = icFileCopy;
  icPlayArrow = icPlayArrow;
  icStop = icStop;

  constructor(
    private cd: ChangeDetectorRef,
    private clipboard: Clipboard,
    private dialog: MatDialog,
    private agentService: AgentService,
    private projectResource: ProjectResource,
    private scenarioResource: ScenarioResource,
    private snackbar: MatSnackBar
  ) {}

  ngOnInit() {
    this.showModes();
  }

// API Access

  showModes() {
    this.agentService.showConfigsModes().subscribe((res: ModesResponse) => {
      this.modes = res;

      this.showProject();
      this.showScenario();
    });
  }

  showProject() {
    const activeMode = this.modes.active;
    const details = this.modes.details[activeMode];

    if (!details.project_id) {
      this.activeScenario = null;
      this.cd.markForCheck();
    } else {
      this.projectResource.show(details.project_id).subscribe((res) => {
        this.activeProject = new Project(res);

        this.cd.markForCheck();
      });
    }
  }

  showScenario() {
    const activeMode = this.modes.active;
    const details = this.modes.details[activeMode];

    if (!details.scenario_id) {
      this.activeScenario = null;
      this.cd.markForCheck();
    } else {
      this.scenarioResource.show(details.scenario_id).subscribe((res) => {
        this.activeScenario = new Scenario(res);

        this.cd.markForCheck();
      });
    }
  }

// View Access

  showRecordSettings() {
    this.dropdownOpen = true;
    this.cd.markForCheck();

    const popoverRef = this.dialog.open(
      ToolbarRecordSettingsComponent, {
        width: '600px',
      }
    );

    const onProjectChangeSub = popoverRef.componentInstance.onProjectChange.subscribe((project) => {
      this.activeProject = project;
      this.cd.markForCheck();
    });

    const onScenarioChangeSub = popoverRef.componentInstance.onScenarioChange.subscribe((scenario) => {
      this.activeScenario = scenario;
      this.cd.markForCheck();
    });

    popoverRef.afterClosed().subscribe(() => {
      this.dropdownOpen = false;

      onProjectChangeSub.unsubscribe();
      onScenarioChangeSub.unsubscribe();

      this.cd.markForCheck();
    });
  }

  showMockSettings() {
    this.dropdownOpen = true;
    this.cd.markForCheck();

    const popoverRef = this.dialog.open(
      ToolbarMockSettingsComponent, {
        width: '600px',
      }
    );

    const onProjectChangeSub = popoverRef.componentInstance.onProjectChange.subscribe((project) => {
      this.activeProject = project;
    });

    const onScenarioChangeSub = popoverRef.componentInstance.onScenarioChange.subscribe((scenario) => {
      this.activeScenario = scenario;
    });

    popoverRef.afterClosed().subscribe(() => {
      this.dropdownOpen = false;

      onProjectChangeSub.unsubscribe();
      onScenarioChangeSub.unsubscribe();

      this.cd.markForCheck();
    });
  }

  setMode(modeVal: string) {
   const mode = {
     active: modeVal,
   };

   mode[modeVal] = {
      enabled: false,
    };

   const body = { mode };

   this.agentService.updateConfig(body).subscribe(res => {
      this.modes.active = modeVal;
      this.modes.enabled = false;

      this.showModes();
    });
  }

  toggleEnabled() {
    const mode = {};

    mode[this.modes.active] = {
      enabled: !this.modes.enabled,
    };

    const body = { mode };

    this.agentService.updateConfig(body).subscribe(res => {
      this.modes.enabled = !this.modes.enabled;
      this.cd.markForCheck();
    });
  }

  copyUrl() {
    this.clipboard.copy(environment.agentUrl);

    this.snackbar.open('URL copied to clipboard!', 'close', {
      duration: 2000,
    });
  }
}
