import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { Platform } from '@angular/cdk/platform';
import { DOCUMENT } from '@angular/common';
import { Component, Inject, LOCALE_ID, Renderer2 } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { Settings } from 'luxon';
import { filter } from 'rxjs/operators';

import icCollections from '@iconify/icons-ic/collections';
import icLink from '@iconify/icons-ic/link';
import icList from '@iconify/icons-ic/list';
import icSettings from '@iconify/icons-ic/settings';
import icLayers from '@iconify/icons-ic/twotone-layers';

import { ConfigName } from '@vex/interfaces/config-name.model';
import { ConfigService } from '@vex/services/config.service';
import { LayoutService } from '@vex/services/layout.service';
import { NavigationService } from '@vex/services/navigation.service';
import { SplashScreenService } from '@vex/services/splash-screen.service';
import { Style, StyleService } from '@vex/services/style.service';

import { LayoutConfigService } from '@layout/services/layout-config.service';
import { ProjectDataService } from '@projects/services/project-data.service';

@Component({
  selector: 'vex-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  title = 'vex';

  constructor(
    @Inject(DOCUMENT) private document: Document,
    @Inject(LOCALE_ID) private localeId: string,
    private configService: ConfigService,
    private layoutService: LayoutService,
    private layoutConfigService: LayoutConfigService,
    private navigationService: NavigationService,
    private platform: Platform,
    private projectDataService: ProjectDataService,
    private renderer: Renderer2,
    private route: ActivatedRoute,
    private router: Router,
    private splashScreenService: SplashScreenService,
    private styleService: StyleService
  ) {
    Settings.defaultLocale = this.localeId;

    if (this.platform.BLINK) {
      this.renderer.addClass(this.document.body, 'is-blink');
    }

    // Set theme configurations
    this.configService.setConfig('vex-layout-zeus' as ConfigName);
    // this.styleService.setStyle('light' as Style);

    // Define navbar
    this.navigationService.items = [
      {
        type: 'link',
        label: 'Projects',
        route: '/projects',
        isVisible: () => false,
        handleClick: () => {
          this.showPath('/projects');
        },
        icon: icCollections,
      },
      {
        type: 'link',
        label: 'Requests',
        route: '/requests',
        isVisible: () => this.isProject(),
        handleClick: () => {
          this.showPath('/requests');
        },
        icon: icLink,
      },
      {
        type: 'link',
        label: 'Scenarios',
        route: '/scenarios',
        isVisible: () => this.isProject(),
        handleClick: () => {
          this.showPath('/scenarios');
        },
        icon: icList,
      },
      {
        type: 'link',
        label: 'Endpoints',
        route: '/endpoints',
        isVisible: () => this.isProject(),
        handleClick: () => {
          this.showPath('/endpoints');
        },
        icon: icLayers,
      },
      // {
      //   type: 'link',
      //   label: 'Aliases',
      //   route: '/aliases',
      //   isVisible: () => this.isProject(),
      //   onclick: () => {
      //     this.showPath('/aliases');
      //   },
      //   icon: icLayers
      // },
      {
        type: 'link',
        label: 'Settings',
        route: '/projects',
        isVisible: () => this.isProject(),
        handleClick: () => {
          this.showSettingsPath();
        },
        icon: icSettings,
      },
    ];

    /**
     * Config Related Subscriptions
     * You can remove this if you don't need the functionality of being able to enable specific configs with queryParams
     * Example: example.com/?layout=apollo&style=default
     */
    this.route.queryParamMap.pipe(
      filter(queryParamMap => queryParamMap.has('rtl') && coerceBooleanProperty(queryParamMap.get('rtl')))
    ).subscribe(queryParamMap => {
      this.document.body.dir = 'rtl';
      this.configService.updateConfig({
        rtl: true,
      });
    });

    this.route.queryParamMap.pipe(
      filter(queryParamMap => queryParamMap.has('layout'))
    ).subscribe(queryParamMap => this.configService.setConfig(queryParamMap.get('layout') as ConfigName));

    this.route.queryParamMap.pipe(
      filter(queryParamMap => queryParamMap.has('style'))
    ).subscribe(queryParamMap => this.styleService.setStyle(queryParamMap.get('style') as Style));
  } // constructor

// View Access

  showPath(path) {
    const project: any = this.projectDataService.project || {};
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const projectId = snapshot.queryParams.project_id || project.id;

    if (path !== this.getResolvedPath(snapshot)) {
      this.router.navigate([path], {
         queryParams: { project_id: projectId },
      });
    }
  }

  showSettingsPath() {
    const project: any = this.projectDataService.project || {};
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const projectId: any = snapshot.queryParams.project_id || project.id;
    const path = `/projects/${projectId}/settings`;

    this.router.navigate([path]);
  }

// Helpers

  getResolvedPath(route: ActivatedRouteSnapshot): string {
    return route.pathFromRoot
        .map(v => v.url.map(segment => segment.toString()).join('/'))
        .join('/');
  }

  isProject() {
    return this.layoutConfigService.isProject();
  }
}
