import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AngularTokenService } from 'angular-token';
import { Observable } from 'rxjs';

import { PopoverRef } from '@vex/components/popover/popover-ref';
import { trackById } from '@vex/utils/track-by';
import { Icon } from '@visurel/iconify-angular';

import { MenuItem } from '../interfaces/menu-item.interface';

import { RouteConstants } from '@data/constants/route.constants';
import { User } from '@data/schema/user';
import { UserDataService } from '@users/services/user-data.service';
import { ToolbarUserDropdownIcons } from './services/toolbar-user-dropdown-icons.service';

export interface OnlineStatus {
  id: 'online' | 'away' | 'dnd' | 'offline';
  label: string;
  icon: Icon;
  colorClass: string;
}

@Component({
  selector: 'vex-toolbar-user-dropdown',
  templateUrl: './toolbar-user-dropdown.component.html',
  styleUrls: ['./toolbar-user-dropdown.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ToolbarUserDropdownComponent implements OnInit {

  items: MenuItem[];

  statuses: OnlineStatus[];
  activeStatus: OnlineStatus;

  trackById = trackById;

  user$: Observable<User>;
  user: User;

  constructor(
    public icons: ToolbarUserDropdownIcons,
    private cd: ChangeDetectorRef,
    private popoverRef: PopoverRef<ToolbarUserDropdownComponent>,
    private router: Router,
    private tokenService: AngularTokenService,
    private userData: UserDataService
  ) { }

  ngOnInit() {
    this.user$ = this.userData.get();
    this.user$.subscribe(user => {
      this.user = user;

      this.items = [
        {
          id: '1',
          icon: this.icons.icAccountCircle,
          label: 'My Profile',
          description: 'Personal Information',
          colorClass: 'text-teal-500',
          route: `/users/${user.id}/profile`,
        },
        // {
        //   id: '2',
        //   icon: icMoveToInbox,
        //   label: 'My Inbox',
        //   description: 'Messages & Latest News',
        //   colorClass: 'text-primary-500',
        //   route: '/apps/chat'
        // },
        {
          id: '3',
          icon: this.icons.icListAlt,
          label: 'My Projects',
          description: 'Tasks & Active Projects',
          colorClass: 'text-amber-500',
          route: '/projects',
        },
        {
          id: '4',
          icon: this.icons.icTableChart,
          label: 'Billing Information',
          description: 'Pricing & Current Plan',
          colorClass: 'text-purple-500',
          route: `/users/${user.id}/billing`,
        },
      ];
    });

    this.statuses = [
      {
        id: 'online',
        label: 'Online',
        icon: this.icons.icCheckCircle,
        colorClass: 'text-green-500',
      },
      {
        id: 'away',
        label: 'Away',
        icon: this.icons.icAccessTime,
        colorClass: 'text-orange-500',
      },
      {
        id: 'dnd',
        label: 'Do not disturb',
        icon: this.icons.icDoNotDisturb,
        colorClass: 'text-red-500',
      },
      {
        id: 'offline',
        label: 'Offline',
        icon: this.icons.icOfflineBolt,
        colorClass: 'text-gray-500',
      },
    ];

    this.activeStatus = this.statuses[0];
  }

  setStatus(status: OnlineStatus) {
    this.activeStatus = status;
    this.cd.markForCheck();
  }

  logout() {

    this.tokenService.signOut().subscribe(res => {
      this.popoverRef.close();
      this.router.navigateByUrl(RouteConstants.LOGIN_PAGE);
    });
  }
}
