export class OrganizationsUser {
  id: number;
  name: string;
  email: string;
  role: string;

  constructor(organizationsUser) {
    this.id = organizationsUser.id;
    this.name = organizationsUser.name;
    this.email = organizationsUser.email;
    this.role = organizationsUser.role;
  }
}

export enum OrganizationsUserRoles {
  organization_owner = 1,
  organization_admin,
  organization_editor,
  organization_viewer,
}

export enum OrganizationsUserRoleTitles {
  Owner = 1,
  Admin,
  Editor,
  Viewer,
}
