import { Injectable } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot } from '@angular/router';

import { AngularTokenService } from 'angular-token';

import { ConfigName } from '@vex/interfaces/config-name.model';
import { ConfigService } from '@vex/services/config.service';
import { Style, StyleService } from '@vex/services/style.service';

import { environment } from '@environments/environment';
import { ProjectDataService } from '@projects/services/project-data.service';

@Injectable({
  providedIn: 'root',
})
export class LayoutConfigService {
  constructor(
    private configService: ConfigService,
    private projectDataService: ProjectDataService,
    private route: ActivatedRoute,
    private styleService: StyleService,
    private tokenService: AngularTokenService
  ) { }

  setIkaros() {
    this.configService.setConfig('vex-layout-ikaros' as ConfigName);
    // this.styleService.setStyle('default' as Style);
  }

  setZeus() {
    this.configService.setConfig('vex-layout-zeus' as ConfigName);
  }

  isProxied() {
    return !!environment.agentUrl;
  }

  isSignedIn() {
    return this.tokenService.userSignedIn();
  }

  isProject() {
    const pathname = location.pathname;

    if (pathname === '/projects') return false;
    
    const pathSegments = pathname.split('/');

    pathSegments.shift(); // Get rid of empty string

    return ['projects', 'requests', 'endpoints', 'scenarios'].includes(pathSegments[0]);
  }
}
