import { Injectable } from '@angular/core';

import { UriService } from '@core/utils/uri.service';
import { Request } from '@data/schema/request';
import { environment } from '@environments/environment';

import { HttpService } from './http.service';

@Injectable({
  providedIn: 'root',
})
export class AgentService {
  DO_PROXY_HEADER = 'X-Do-Proxy';
  SERVICE_URL_HEADER = 'X-Service-Url';
  RECORD_POLICY_HEADER = 'X-Record-Policy';
  MOCK_POLICY_HEADER = 'X-Mock-Policy';
  RECORD_NONE_POLICY = 'record_any';

  CONFIGS_ENDPOINT = '/api/v1/admin/configs';
  STATUSES_ENDPOINT = '/api/v1/admin/statuses';

  constructor(
    private httpService: HttpService,
    private uri: UriService
  ) {

  }

  showStatus(statusId: string) {
    return this.sendGetRequest(`${this.STATUSES_ENDPOINT}/${statusId}`);
  }

  showConfigsModes() {
    return this.sendGetRequest(`${this.CONFIGS_ENDPOINT}/modes`);
  }

  showConfigsPolicies() {
    return this.sendGetRequest(`${this.CONFIGS_ENDPOINT}/policies`);
  }

  showConfig(queryParams?: any) {
    return this.sendGetRequest(this.CONFIGS_ENDPOINT, queryParams);
  }

  createConfig(body: object) {
    return this.sendPostRequest(this.CONFIGS_ENDPOINT, body);
  }

  updateConfig(body: object) {
    return this.sendPutRequest(`${this.CONFIGS_ENDPOINT}`, body);
  }

  sendRequest(request: Request) {
    const uri = new this.uri.class(request.url);

    const headers = { SERVICE_URL_HEADER: uri.origin };

    if (request.headers) {
      request.headers.forEach(header => {
        headers[header.name] = header.value;
      });
    }

    const queryParams = {};

    if (request.queryParams) {
      request.queryParams.forEach(queryParam => {
        queryParams[queryParam.anme] = queryParam.value;
      });
    }

    let observable;

    switch (request.method) {
      case 'GET':
        observable = this.sendGetRequest(
          uri.pathname, queryParams, { headers }
        );
    }

    return observable;
  }

  sendGetRequest(path: string, queryParams?, options?) {
    const url = `${environment.agentUrl}${path}`;
    return this.httpService.get(url, queryParams, options);
  }

  sendPostRequest(path: string, body, options?) {
    const url = `${environment.agentUrl}${path}`;
    return this.httpService.post(url, body, options);
  }

  sendPutRequest(path: string, body, options?) {
    const url = `${environment.agentUrl}${path}`;
    return this.httpService.put(url, body, options);
  }
}
