import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Event, NavigationEnd, Router }  from '@angular/router';

import { Observable } from 'rxjs';

import { PopoverService } from '@vex/components/popover/popover.service';
import theme from '@vex/utils/tailwindcss';

import icPerson from '@iconify/icons-ic/twotone-person';

import { User } from '@data/schema/user';
import { UserDataService } from '@users/services/user-data.service';
import { ToolbarUserDropdownComponent } from './toolbar-user-dropdown/toolbar-user-dropdown.component';

@Component({
  selector: 'vex-toolbar-user',
  templateUrl: './toolbar-user.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ToolbarUserComponent implements OnInit {

  dropdownOpen: boolean;
  icPerson = icPerson;

  theme = theme;

  user$: Observable<User> = this.userData.get();

  constructor(
    private cd: ChangeDetectorRef,
    private popover: PopoverService,
    private userData: UserDataService,
    private router: Router
  ) { }

  ngOnInit() {

  }

  showPopover(originRef: HTMLElement) {
    this.dropdownOpen = true;
    this.cd.markForCheck();

    const popoverRef = this.popover.open({
      content: ToolbarUserDropdownComponent,
      origin: originRef,
      offsetY: 12,
      position: [
        {
          originX: 'center',
          originY: 'top',
          overlayX: 'center',
          overlayY: 'bottom',
        },
        {
          originX: 'end',
          originY: 'bottom',
          overlayX: 'end',
          overlayY: 'top',
        },
      ],
    });

    // Listen to route change event, close popper when route changes
    this.router.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        popoverRef.close();
      }
    });

    popoverRef.afterClosed$.subscribe(() => {
      this.dropdownOpen = false;
      this.cd.markForCheck();
    });
  }
}
